













































import Vue from 'vue';

type titles = {
    [key: string]: string;
};

const routingTitles: titles = {
    '': '',
    login: 'Авторизация',
    home: 'Домашний экран',
    profile: 'Профиль',
    timeline: 'График',
    dashboard: 'Дашборд',
    chat: 'Сообщения'
};

export default Vue.extend({
    props: ['drawer'],
    data() {
        return {
            navigationItems: [
                {
                    title: 'Profile',
                    id: 'profile',
                    icon: 'account_circle'
                },
                {
                    title: 'Home',
                    id: 'home',
                    icon: 'home'
                },
                {
                    title: 'Kanban',
                    id: 'kanbanBoards',
                    icon: 'table_chart'
                }
            ],
            selectedModule: '',
            isDrawerOpen: this.drawer
        };
    },
    watch: {
        drawer: {
            handler(val, oldVal) {
                this.isDrawerOpen = !this.isDrawerOpen;
            }
        }
    },
    methods: {
        navigateTo(newModule: any) {
            switch (newModule) {
                case 'home': {
                    this.selectedModule = 'home';
                    break;
                }
                case 'profile': {
                    this.selectedModule = 'profile';
                    break;
                }
                case 'kanbanBoards': {
                    this.selectedModule = 'KanbanBoards';
                    break;
                }
                default:
                    break;
            }

            this.navigateToModule(this.selectedModule);
        },

        navigateToModule(moduleId: string) {
            this.$router.push({ name: this.selectedModule });
            this.isDrawerOpen = false;
        }
    },
    computed: {
        profile(): any {
            return this.$store.state.ProfileStore;
        }
    }
});
